<template>
  <div id="writeDiary">
    <div class="nowDay"><img src="../../assets/icon/calendar.png" alt="">{{newDate}}</div>
    <mavon-editor :toolbars="toolbars"
                  ref=md
                  :ishljs="true"
                  :codeStyle="'vs2015'"
                  @imgAdd="$imgAdd"
                  @imgDel="$imgDel"
                  style="min-height:55vh;max-height:55vh;z-index:1;margin-top:25px" />
    <person-center-btn name=''
                       @Submit="issueDiary"></person-center-btn>
                       <common-hint :hint="hint" :show="showHint"></common-hint>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import { officeDiary } from '../../api/diary'
import { createFile, delFile } from '../../api/_file'
import PersonCenterBtn from '../commonBtn/personCenterBtn.vue'
import CommonHint from '../commonHints/commonHint.vue'
export default {
  components: { mavonEditor, PersonCenterBtn, CommonHint },
  data () {
    return {
      hint: null,
      newDate: null,
      showHint: 0,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: false, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
    }
  },
  methods: {
    $imgAdd (pos, $file) {
      const data = { base64: $file.miniurl.split(',')[1].replace('-', '+').replace('_', '/') }
      const file = createFile($file.name, data)
      file.save().then(file => {
        this.$refs.md.$img2Url(pos, file.attributes.url)
      })
    },
    $imgDel (pos) {
      delFile(pos[0].replace('http://lc-uzUuUouk.cn-n1.lcfile.com/', ''))
    },
    issueDiary () {
      if (this.$refs.md.d_value.replace().length !== 0) {
        officeDiary(this.$refs.md.d_render).then(res => {
          this.$router.push('/diary')
        }, error => {
          if (error) {
            this.hint = ''
            this.showHint++
          }
        })
      } else {
        this.hint = ''
        this.showHint++
      }
    }
  },
  created () {
    const date = new Date()
    this.newDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
  }
}
</script>

<style scoped>
.nowDay {
  font-size: 25px;
}
.nowDay img{
  height: 30px;
  margin-right: 20px;
}
</style>
