<template>
  <div class="popContainer" @click="$emit('show', false)">
    <div class="cropper-content" @click.stop="">
      <div class="cropper-box">
        <div class="cropper">
          <vue-cropper ref="cropper"
                       :img="option.img"
                       :outputSize="option.outputSize"
                       :outputType="option.outputType"
                       :info="option.info"
                       :canScale="option.canScale"
                       :autoCrop="option.autoCrop"
                       :autoCropWidth="option.autoCropWidth"
                       :autoCropHeight="option.autoCropHeight"
                       :fixed="option.fixed"
                       :fixedNumber="option.fixedNumber"
                       :full="option.full"
                       :fixedBox="option.fixedBox"
                       :canMove="option.canMove"
                       :canMoveBox="option.canMoveBox"
                       :original="option.original"
                       :centerBox="option.centerBox"
                       :height="option.height"
                       :infoTrue="option.infoTrue"
                       :maxImgSize="option.maxImgSize"
                       :enlarge="option.enlarge"
                       :mode="option.mode"
                       @realTime="realTime"
                       @imgLoad="imgLoad">
          </vue-cropper>
        </div>
      </div>
      <!---->
      <div class="show-preview">
        <div class="show-preview-one">
          <div :style="previews.div"
               class="preview">
            <img :src="previews.url"
                 :style="previews.img">
          </div>
        </div>
        <div class="show-preview-two">
          <div :style="previews.div"
               class="preview">
            <img :src="previews.url"
                 :style="previews.img">
          </div>
        </div>
      </div>
      <!---->
      <div class="footer-btn">
        <label class="btn"
               for="uploads"></label>
        <input type="file"
               id="uploads"
               style="position:absolute; clip:rect(0 0 0 0);"
               accept="image/png, image/jpeg, image/gif, image/jpg"
               @change="selectImg($event)">
        <span @click="changeScale(1)"></span>
        <span @click="changeScale(-1)"></span>
        <span @click="rotateLeft"> </span>
        <span @click="rotateRight"> </span>
        <span @click="uploadImg('base64')"></span>
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
export default {
  name: 'CropperImage',
  components: {
    VueCropper
  },
  props: ['Name'],
  data () {
    return {
      name: this.Name,
      previews: {},
      option: {
        img: '', // 
        outputSize: 1, // (0.1 - 1)
        outputType: 'jpeg', // jpeg || png || webp
        info: true, // 
        canScale: true, // 
        autoCrop: true, // 
        autoCropWidth: 175, // 
        autoCropHeight: 175, // 
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: false, // false
        fixedBox: true, // 
        canMove: false, // 
        canMoveBox: true, // 
        original: false, // 
        centerBox: false, // 
        height: true, // dpr 
        infoTrue: false, // truefalse
        maxImgSize: 3000, // 
        enlarge: 2, // 
        mode: 'contain 100% auto' // 
      }
    }
  },
  methods: {
    // 
    imgLoad (msg) {
      console.log('=====' + msg)
    },
    // 
    changeScale (num) {
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    // 
    rotateLeft () {
      this.$refs.cropper.rotateLeft()
    },
    // 
    rotateRight () {
      this.$refs.cropper.rotateRight()
    },
    // 
    realTime (data) {
      this.previews = data
    },
    // 
    selectImg (e) {
      const file = e.target.files[0]
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        this.$message({
          message: 'jpegjpgpng',
          type: 'error'
        })
        return false
      }
      // blob
      const reader = new FileReader()
      reader.onload = (e) => {
        let data
        if (typeof e.target.result === 'object') {
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      // base64
      reader.readAsDataURL(file)
    },
    // 
    uploadImg (type) {
      if (type === 'base64') {
        this.$refs.cropper.getCropData(async (data) => {
          this.$emit('photoImage', data)
        })
      }
    }
  }
}
</script>

<style scoped>
.cropper-content {
  position: fixed;
  width: 600px;
  height: 450px;
  left: calc(50%);
  top: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 25px;
}
.cropper-box {
  width: 400px;
  display: inline-block;
}
.cropper {
  width: auto;
  height: 400px;
}
.show-preview {
  display: inline-block;
  width: 175px;
  height: 380px;
  position: absolute;
  right: 25px;
  background: rgb(143, 141, 141);
  padding: 10px;
}
.preview {
  overflow: hidden;
  display: inline-block;
}
.show-preview-one {
  width: 175px;
  height: 175px;
  margin-bottom: 25px;
}
.show-preview-two {
  width: 175px;
  height: 175px;
  border-radius: 50%;
  overflow: hidden;
}
.footer-btn {
  height: 70px;
  display: -webkit-flex;
  flex-wrap: nowrap;
  justify-content: center;
}
.footer-btn span,
.btn {
  line-height: 50px;
  background: tomato;
  margin: 10px 7px;
  padding: 0 10px;
  cursor: pointer;
  font-weight: bolder;
  color: white;
  border-radius: 5px;
}
.footer-btn span:hover,
.btn:hover {
  background: #4af8f8;
  color: black;
}
.upload-btn {
  flex: 1;
  -webkit-flex: 1;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
}
</style>
